.home-wrapper {
    background-image: url("../../assets/home-bg.png");
    background-repeat: no-repeat;
    background-size: auto 100%;
    background-attachment: fixed;
    background-position: top right;
}
.exit-icon {
    width: 30px;
    height: 30px;
}
.home-item {
    color: #fff;
    cursor: pointer;
    font-weight: bold;
    padding: 15px 25px;
    padding-left: 10px;
    padding-bottom: 5px;
    border-radius: 15px;
    background-color: #2196f3;
    box-shadow: 0 9px #0d89ed;
    background-repeat: no-repeat;
    background-position: top right;
}

